    export default {
        name: 'pick-gift',

        props: {
			theme: String,
			gift_list: Array,
        },

        data() {
            return {
                index: -1,
            }
        },

	    methods: {
            
            // 加减改变数量
            changeNumber(status, index) {
                let quantity = 0;
                status ? quantity = 1 : quantity = -1;
                this.$emit('giftNumber', {
                    index: index, quantity: quantity, type : status
                });
            },

		    // input改变数量
            changeInput(e) {
                this.$emit('giftInput', {
                    index: this.index, quantity: Number(e.detail.value)
                });
            },
			
		    // input获取下标
            changeIndex(index) {
                this.index = index;
            },

		    // 跳转到商品列表
            routeGo(data) {
                uni.navigateTo({
	                url: data,
                })
            },
			routeRule() {
				uni.navigateTo({
					url: `/pages/rules/index?url=${encodeURIComponent(this.$api.gift.config)}&key=explain`
				});
			}
	    },
	    
	    filters: {
            getGiftNumber(data) {
                let number = 0;
                for (let i = 0; i < data.length; i++) {
                    number += Number(data[i].number);
                }
                return number;
            }
	    }
    }
